"use strict"

import { initInputMode } from './initMode'
import { initDropdowns } from './dropdownMenu'
import { initEffects } from './initEffects.js'
import { initUtils } from './initUtils'
import { formUtils } from './formUtils'
import { movingElements } from './movingElement'
import { openModal } from './modal'
import { initLoader } from './initLoader'


// ===========================================================================================
// -----------------------------
// –ì–û–õ–û–í–ù–ò–ô –ó–ê–ü–£–°–ö
// -----------------------------
function initApp() {
   initLoader()
   initInputMode()
   initDropdowns()
   initEffects()
   movingElements()
   // formUtils()
   initUtils()
   openModal()
   initLoadAnimation()
}

if (document.readyState === 'loading') {
   document.addEventListener('DOMContentLoaded', initApp);
} else {
   initApp();
}


const PageLock = {
   lock() {
      // –î–æ–¥–∞—î–º–æ –∫–ª–∞—Å –Ω–∞ –æ–±–∏–¥–≤–∞ –µ–ª–µ–º–µ–Ω—Ç–∏
      document.documentElement.classList.add('is-locked');
      document.body.classList.add('is-locked');
   },
   unlock() {
      document.documentElement.classList.remove('is-locked');
      document.body.classList.remove('is-locked');
   }
};

