"use strict"

export function initUtils() {
   slidersInit()
   document.querySelectorAll('[data-filter]').forEach(initFilter);
}

// ===========================================================================================
// -----------------------------
// current-page
// -----------------------------
document.addEventListener("DOMContentLoaded", function () {
   const currentUrl = window.location.href;
   const navLinks = document.querySelectorAll('.menu-header__link');

   navLinks.forEach(link => {
      const linkHref = link.getAttribute('href');

      // 1. –Ü–≥–Ω–æ—Ä—É—î–º–æ –ø–æ—Å–∏–ª–∞–Ω–Ω—è, —è–∫—ñ —î –ø—Ä–æ—Å—Ç–æ –∑–∞–≥–ª—É—à–∫–∞–º–∏ –¥–ª—è –º–µ–Ω—é (#)
      if (linkHref === "#" || linkHref === "") {
         return;
      }

      // 2. –Ü–≥–Ω–æ—Ä—É—î–º–æ –≥–æ–ª–æ–≤–Ω—É —Å—Ç–æ—Ä—ñ–Ω–∫—É (—è–∫—â–æ URL –∑–∞–∫—ñ–Ω—á—É—î—Ç—å—Å—è –Ω–∞ / –∞–±–æ index.html)
      // –í–∏–¥–∞–ª—ñ—Ç—å —Ü—é —É–º–æ–≤—É, —è–∫—â–æ –Ω–∞ –≥–æ–ª–æ–≤–Ω—ñ–π –≤—Å–µ –∂ —Ç–∞–∫–∏ –ø–æ—Ç—Ä—ñ–±–Ω–∞ –ø—ñ–¥—Å–≤—ñ—Ç–∫–∞
      const isHomePage = window.location.pathname === "/" || window.location.pathname.endsWith("index.html");
      if (isHomePage) {
         return;
      }

      // 3. –ü–æ—Ä—ñ–≤–Ω—é—î–º–æ URL –¥–ª—è –≤—Å—ñ—Ö —ñ–Ω—à–∏—Ö —Å—Ç–æ—Ä—ñ–Ω–æ–∫
      if (link.href === currentUrl) {
         link.classList.add('current-page');
      }
   });
});

// ===========================================================================================
// ===========================================================================================
// -----------------------------
// SLIDER
// -----------------------------
function slidersInit() {
   if (document.querySelector('.swiper--side')) {
      const swiper = new Swiper('.swiper--side', {
         loop: true,
         slidesPerView: 4,
         spaceBetween: 10,
         freeMode: true,
         watchSlidesProgress: true,
         // centteredSlides: false,

         // navigation: {
         //    nextEl: ".button-arrow--next",
         //    prevEl: ".button-arrow--prev",
         // },


      });

      var swiper2 = new Swiper(".swiper--main", {
         loop: true,
         spaceBetween: 10,
         navigation: {
            nextEl: ".button-arrow--next",
            prevEl: ".button-arrow--prev",
         },
         thumbs: {
            swiper: swiper,
         },
      });
   }
}

// ===========================================================================================
// -----------------------------
// accordion-menu
// -----------------------------
document.addEventListener("DOMContentLoaded", () => {
   const accordions = document.querySelectorAll("[data-accordion]");

   accordions.forEach((accordion) => {
      const btn = accordion.querySelector("[data-accordion-btn]");
      const body = accordion.querySelector("[data-accordion-body]");

      btn.addEventListener("click", (e) => {
         e.preventDefault();

         const isOpen = accordion.classList.contains("is-open");

         if (!isOpen) {
            // –í–Ü–î–ö–†–ò–¢–¢–Ø
            accordion.classList.add("is-open");
            // –í—Å—Ç–∞–Ω–æ–≤–ª—é—î–º–æ –≤–∏—Å–æ—Ç—É –≤ px –¥–ª—è –∑–∞–ø—É—Å–∫—É transition
            body.style.height = `${body.scrollHeight}px`;

            // –ü—ñ—Å–ª—è –∑–∞–≤–µ—Ä—à–µ–Ω–Ω—è –∞–Ω—ñ–º–∞—Ü—ñ—ó —Å—Ç–∞–≤–∏–º–æ auto –¥–ª—è –∞–¥–∞–ø—Ç–∏–≤–Ω–æ—Å—Ç—ñ
            body.addEventListener("transitionend", function handler() {
               if (accordion.classList.contains("is-open")) {
                  body.style.height = "auto";
               }
               body.removeEventListener("transitionend", handler);
            }, { once: true });

         } else {
            // –ó–ê–ö–†–ò–¢–¢–Ø
            // –°–ø–æ—á–∞—Ç–∫—É –º—ñ–Ω—è—î–º–æ auto –Ω–∞ —Ñ—ñ–∫—Å–æ–≤–∞–Ω—ñ px
            body.style.height = `${body.scrollHeight}px`;

            // –í–∏–∫–ª–∏–∫–∞—î–º–æ –ø–µ—Ä–µ—Ä–∞—Ö—É–Ω–æ–∫ (force reflow)
            body.offsetHeight;

            // –ó–∞–ø—É—Å–∫–∞—î–º–æ –∞–Ω—ñ–º–∞—Ü—ñ—é –¥–æ 0
            requestAnimationFrame(() => {
               body.style.height = "0";
               accordion.classList.remove("is-open");
            });
         }
      });
   });
});


// ===========================================================================================
// -----------------------------
// filter
// -----------------------------
/**
 * –ü–ª–∞–≤–Ω–∏–π —Å–∫—Ä–æ–ª –¥–æ –µ–ª–µ–º–µ–Ω—Ç–∞ –∑ —É—Ä–∞—Ö—É–≤–∞–Ω–Ω—è–º offset
 * @param {HTMLElement} target - –µ–ª–µ–º–µ–Ω—Ç, –¥–æ —è–∫–æ–≥–æ —Å–∫—Ä–æ–ª–∏–º–æ
 * @param {HTMLElement} root - –∫–æ–Ω—Ç–µ–π–Ω–µ—Ä –∑ data-–Ω–∞–ª–∞—à—Ç—É–≤–∞–Ω–Ω—è–º–∏
 * @param {number} [delay=0] - –∑–∞—Ç—Ä–∏–º–∫–∞ –ø–µ—Ä–µ–¥ —Å–∫—Ä–æ–ª–æ–º
 */
function smoothScrollTo(target, root, delay = 0) {
   if (!target || !root) return;

   const offset = +root.dataset.scrollOffset || 0;

   const startScroll = () => {
      const targetTop = target.getBoundingClientRect().top + window.scrollY;
      const scrollPosition = targetTop - offset;

      window.scrollTo({
         top: scrollPosition,
         behavior: 'smooth'
      });
   };

   delay ? setTimeout(startScroll, delay) : startScroll();
}

/**
 * –Ü–Ω—ñ—Ü—ñ–∞–ª—ñ–∑–∞—Ü—ñ—è —Ñ—ñ–ª—å—Ç—Ä—É –∫–∞—Ä—Ç–æ–∫
 * @param {HTMLElement} root - –µ–ª–µ–º–µ–Ω—Ç –∑ data-filter
 */
function initFilter(root) {
   if (!root) return;

   const buttons = root.querySelectorAll('[data-filter-btn]');
   const items = Array.from(root.querySelectorAll('[data-filter-item]'));
   const btnMore = root.querySelector('[data-filter-more-btn="more"]');
   const btnLess = root.querySelector('[data-filter-more-btn="less"]');

   const visibleDefault = +root.dataset.visible || items.length;
   const step = +root.dataset.step || visibleDefault;

   let currentCategory = 'all';
   let visibleCount = visibleDefault;

   const matchCategory = (item) => {
      if (currentCategory === 'all') return true;
      return item.dataset.category?.split(' ').includes(currentCategory);
   };

   const updateItems = (isShowMore = false) => {
      // 1. –û—Ç—Ä–∏–º—É—î–º–æ —Å–ø–∏—Å–æ–∫ –∫–∞—Ä—Ç–æ–∫, —â–æ –ø—ñ–¥—Ö–æ–¥—è—Ç—å –ø—ñ–¥ –∫–∞—Ç–µ–≥–æ—Ä—ñ—é
      const matchedItems = items.filter(matchCategory);
      const totalMatched = matchedItems.length;

      // 2. –í–∏–∑–Ω–∞—á–∞—î–º–æ, —è–∫—ñ –∑ –Ω–∏—Ö –º–∞—é—Ç—å –±—É—Ç–∏ –ø–æ–∫–∞–∑–∞–Ω—ñ
      matchedItems.forEach((item, index) => {
         if (index < visibleCount) {
            // –ï–ª–µ–º–µ–Ω—Ç –º–∞—î –±—É—Ç–∏ –≤–∏–¥–∏–º–∏–º
            item.hidden = false;
            item.style.transitionDelay = isShowMore && index >= (visibleCount - step)
               ? `${((index - (visibleCount - step)) % step) * 0.15}s`
               : '0s';

            requestAnimationFrame(() => {
               item.classList.add('is-visible');
            });
         } else {
            // –ï–ª–µ–º–µ–Ω—Ç –º–∞—î –±—É—Ç–∏ –ø—Ä–∏—Ö–æ–≤–∞–Ω–∏–π
            item.classList.remove('is-visible');
            item.style.transitionDelay = '0s';

            // –í–∏–∫–æ—Ä–∏—Å—Ç–æ–≤—É—î–º–æ –ø–µ—Ä–µ–≤—ñ—Ä–∫—É transitionend –∞–±–æ —Ñ—ñ–∫—Å–æ–≤–∞–Ω–∏–π —á–∞—Å
            setTimeout(() => {
               if (!item.classList.contains('is-visible')) {
                  item.hidden = true;
               }
            }, 400);
         }
      });

      // 3. –ü—Ä–∏—Ö–æ–≤—É—î–º–æ —Ç—ñ –∫–∞—Ä—Ç–∫–∏, —è–∫—ñ –≤–∑–∞–≥–∞–ª—ñ –Ω–µ –ø—ñ–¥–ø–∞–¥–∞—é—Ç—å –ø—ñ–¥ –∫–∞—Ç–µ–≥–æ—Ä—ñ—é (–±–µ–∑ –∑–∞—Ç—Ä–∏–º–æ–∫)
      items.filter(item => !matchCategory(item)).forEach(item => {
         item.classList.remove('is-visible');
         item.hidden = true;
      });

      // 4. –û–ù–û–í–õ–ï–ù–ù–Ø –ö–ù–û–ü–û–ö (—Ä–∞—Ö—É—î–º–æ –ø–æ –ª–æ–≥—ñ—á–Ω–∏—Ö –º–∞—Å–∏–≤–∞—Ö)
      // –ö–Ω–æ–ø–∫–∞ More: –ø–æ–∫–∞–∑—É—î–º–æ, —è–∫—â–æ —â–µ —î —â–æ –≤—ñ–¥–∫—Ä–∏–≤–∞—Ç–∏ –≤ —Ü—ñ–π –∫–∞—Ç–µ–≥–æ—Ä—ñ—ó
      if (btnMore) {
         btnMore.hidden = visibleCount >= totalMatched;
      }

      // –ö–Ω–æ–ø–∫–∞ Less: –ø–æ–∫–∞–∑—É—î–º–æ, —è–∫—â–æ –∑–∞—Ä–∞–∑ –ø–æ–∫–∞–∑–∞–Ω–æ –±—ñ–ª—å—à–µ, –Ω—ñ–∂ —Å—Ç–∞–Ω–¥–∞—Ä—Ç–Ω–∏–π –º—ñ–Ω—ñ–º—É–º
      if (btnLess) {
         btnLess.hidden = visibleCount <= visibleDefault || totalMatched <= visibleDefault;
      }

      // –ê–≤—Ç–æ—Å–∫—Ä–æ–ª
      if (isShowMore && matchedItems[visibleCount - step]) {
         setTimeout(() => {
            smoothScrollTo(matchedItems[visibleCount - step], root);
         }, 150);
      }
   };

   // –ö–Ω–æ–ø–∫–∏ –∫–∞—Ç–µ–≥–æ—Ä—ñ–π
   buttons.forEach(btn => {
      btn.addEventListener('click', () => {
         currentCategory = btn.dataset.filterBtn;
         visibleCount = visibleDefault;

         buttons.forEach(b => b.classList.remove('is-active'));
         btn.classList.add('is-active');

         updateItems();

         // –°–∫—Ä–æ–ª –¥–æ –ø–æ—á–∞—Ç–∫—É —Ñ—ñ–ª—å—Ç—Ä—É
         setTimeout(() => smoothScrollTo(root, root), 100);
      });
   });

   // Show more
   btnMore?.addEventListener('click', () => {
      visibleCount += step;
      updateItems(true);
   });

   // Show less
   btnLess?.addEventListener('click', () => {
      visibleCount = visibleDefault;
      updateItems();

      // –ü–ª–∞–≤–Ω–∏–π —Å–∫—Ä–æ–ª –¥–æ –ø–æ—á–∞—Ç–∫—É —Ñ—ñ–ª—å—Ç—Ä—É
      setTimeout(() => smoothScrollTo(root, root), 100);
   });

   // –ü–æ—á–∞—Ç–∫–æ–≤–µ –æ–Ω–æ–≤–ª–µ–Ω–Ω—è
   updateItems();
}

// –Ü–Ω—ñ—Ü—ñ–∞–ª—ñ–∑–∞—Ü—ñ—è –≤—Å—ñ—Ö —Ñ—ñ–ª—å—Ç—Ä—ñ–≤ –Ω–∞ —Å—Ç–æ—Ä—ñ–Ω—Ü—ñ
document.querySelectorAll('[data-filter]').forEach(initFilter);
















